@use "../base/font-variable" as variable_font;
@use "../base/color-variable" as variable;
@use "../component/button" as button;
@use "../component/transition/burger-navigation" as burger_navigation;
@use "../component/transition/dekstop-navigation" as dekstop_navigation;
@use "../component/transition/popup" as popup;

@use "../component/position" as position;
@use "../component/display" as display;
@use "../component/galery" as galery;
@use "../component/other" as other;

@mixin media-query($section) {
  @media only screen and (max-width: 399.98px) {
    @if $section == "navigation" {
      padding: 0px 30px;
      @include display.flex-box($align: center, $justify: space-between);
      .dekstop-menu {
        display: none;
      }
      .mobile-menu {
        @include burger_navigation.burger-navigation();
        .mobile-nav {
          position: absolute;
          top: 0.5em;
          width: 200px;
          right: 1em;
          background-color: rgb(240, 240, 240);
          border-radius: 10px;
          margin-top: 68px;
          box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.08),
            0 6px 10px 0 rgba(0, 0, 0, 0.08);

          z-index: 99;
          &::before {
            position: absolute;
            top: 0;
            right: 24px;
            transform: translateY(-12px);
            content: "";
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0 10px 12px 10px;
            border-color: transparent
              transparent
              rgb(240, 240, 240)
              transparent;
          }
          li {
            margin: 0.8em 1.2em;
            a {
              &.active {
                text-decoration: underline;
                text-underline-offset: 5px;
              }
              text-decoration: none;
              color: inherit;
              font-size: 0.9rem;
            }
          }
        }
      }
    } @else if $section == "hero" {
      @include display.flex-box($direction: column, $align: center);
      .hero-left {
        flex: 1;
        margin: 0 !important;
        .my-profile {
          display: none;
        }
      }
      .hero-right {
        flex: 1;
        h2 {
          display: block !important;
        }
        small {
          margin: 0 !important;
        }
        span {
          margin-top: 1em;
        }
        .call-to-action {
          @include display.flex-box(
            $direction: column !important,
            $align: flex-start !important
          );
          .social {
            margin-top: 10px;
          }
          a {
            margin: 0 !important;
            margin-bottom: 10px !important;
          }
        }
      }
    } @else if $section == "galery" {
      @include galery.flex-galery(
        $direction: row,
        $wrap: wrap,
        $r-gap: 40px,
        $c-gap: 20px,
        $f-basis: 550px
      );
    } @else if $section == "galery-features" {
      display: flex;
      flex-direction: column;
    } @else if $section == "galery-list" {
      @include galery.grid-galery();
    } @else if $section == "detail-description" {
      flex-direction: column;
    } @else if $section == "footer" {
      padding: 1.5em;
    }
  }
  @media only screen and (min-width: 400px) and (max-width: 599.98px) {
    @if $section == "navigation" {
      padding: 0px 30px;
      @include display.flex-box($align: center, $justify: space-between);
      .dekstop-menu {
        display: none;
      }
