            0 6px 10px 0 rgba(0, 0, 0, 0.08);

          z-index: 99;
          &::before {
            position: absolute;
            top: 0;
            right: 24px;
            transform: translateY(-12px);
            content: "";
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0 10px 12px 10px;
            border-color: transparent
              transparent
              rgb(240, 240, 240)
              transparent;
          }
          li {
            margin: 0.8em 1.2em;
            a {
              &.active {
                text-decoration: underline;
                text-underline-offset: 5px;
              }
              text-decoration: none;
              color: inherit;
              font-size: 0.9rem;
            }
          }
        }
      }
    } @else if $section == "hero" {
      @include display.flex-box($direction: column, $align: center);
      .hero-left {
        flex: 1;
        margin-bottom: 1em !important;
        .my-profile {
          max-width: 250px;
        }
      }
      .hero-right {
        flex: 1;
        margin-top: 1em !important;
      }
    } @else if $section == "galery" {
      @include galery.flex-galery(
        $direction: row,
        $wrap: wrap,
        $r-gap: 40px,
        $c-gap: 20px,
        $f-basis: 550px
      );
    } @else if $section == "galery-features" {
      display: flex;
      flex-direction: column;
    } @else if $section == "galery-list" {
