    } @else if $section == "detail-description" {
      flex-direction: column;
    } @else if $section == "footer" {
      padding: 2.5em;
    }
  }
  @media only screen and (min-width: 600px) and (max-width: 767.98px) {
    @if $section == "navigation" {
      padding: 0px 30px;
      @include display.flex-box($align: center, $justify: space-between);
      .dekstop-menu {
        display: none;
      }
      .mobile-menu {
        @include burger_navigation.burger-navigation();
        .mobile-nav {
