        }
      }
    } @else if $section == "hero" {
      @include display.flex-box($direction: column, $align: center);
      .hero-left {
        flex: 1;
      }
      .hero-right {
        flex: 1;
        margin-top: 0 !important;
      }
    } @else if $section == "galery" {
      @include galery.flex-galery(
        $direction: row,
        $wrap: wrap,
        $r-gap: 40px,
