  }
  @media only screen and (min-width: 768px) and (max-width: 991.98px) {
    @if $section == "navigation" {
      padding: 0px 70px;
      @include display.flex-box($align: center, $justify: space-between);
      .dekstop-menu {
        @include dekstop_navigation.dekstop-navigation();
      }
      .mobile-menu {
        display: none;
      }
    } @else if $section == "hero" {
      @include display.flex-box($direction: row, $align: center);
      .hero-left {
        flex: 1;
      }
      .hero-right {
        flex: 2;
        h2 {
          display: block !important;
        }
        small {
          margin: 0 !important;
        }
        span {
          margin-top: 1em;
        }
      }
    } @else if $section == "galery" {
      @include galery.flex-galery(
        $direction: row,
        $wrap: wrap,
        $r-gap: 40px,
        $c-gap: 20px,
        $f-basis: 48%,
        $justify: space-between
      );
    } @else if $section == "galery-features" {
      display: flex;
      flex-direction: row;
    } @else if $section == "galery-list" {
      @include galery.grid-galery();
    } @else if $section == "detail-description" {
      flex-direction: row;
    } @else if $section == "footer" {
      padding: 2.5em;
    }
  }
  @media only screen and (min-width: 992px) {
    @if $section == "navigation" {
      padding: 0px 70px;
      @include display.flex-box($align: center, $justify: space-between);
      .dekstop-menu {
        @include dekstop_navigation.dekstop-navigation();
      }
      .mobile-menu {
        display: none;
      }
    } @else if $section == "hero" {
      @include display.flex-box($direction: row, $align: center);
      .hero-left {
        flex: 1;
      }
      .hero-right {
        flex: 1;
      }
    } @else if $section == "galery" {
      @include galery.flex-galery(
        $direction: row,
        $wrap: wrap,
        $r-gap: 60px,
        $c-gap: 30px,
        $f-basis: 22%,
        $justify: space-between
      );
    } @else if $section == "galery-features" {
      display: flex;
      flex-direction: row;
    } @else if $section == "galery-list" {
      @include galery.grid-galery();
    } @else if $section == "detail-description" {
      flex-direction: row;
    } @else if $section == "footer" {
      padding: 2.5em 4em;
    }
  }
  @media only screen and (min-width: 1200px) {
    @if $section == "hero" {
    } @else if $section == "galery" {
      @include galery.flex-galery(
        $direction: row,
        $wrap: wrap,
        $r-gap: 60px,
        $c-gap: 30px,
        $f-basis: 23%,
        $justify: space-between
      );
    } @else if $section == "galery-list" {
      @include galery.grid-galery();
    } @else if $section == "footer" {
      padding: 2.5em 4em;
    } @else if $section == "grid-footer" {
      grid-template-columns: repeat(1, 40%) repeat(3, 15%);
      gap: 2em;
    }
  }
}
