@use "../../component/media-query" as media-query;
@use "../../base/color-variable" as variable;
@use "../../base/font-variable" as variable-font;
@use "../../component/button" as button;

@use "../../component/position" as position;
@use "../../component/display" as display;
@use "../../component/galery" as galery;
@use "../../component/other" as other;

@use "../../component/form" as form;

@mixin home-section {
  section {
    color: map-get(variable.$primary-color, "text-section-color");
    &:nth-child(1) {
      background-color: map-get(variable.$primary-color, "hero-section");
      .hero {
        max-width: 1200px;
        margin: 0 auto;
        min-height: 450px;
        @include media-query.media-query("hero");
        .hero-left {
          // background-color: rgb(37, 37, 37);
          max-width: 400px;
          margin: 2.5em;
          @include display.flex-box($justify: center);
          // background-color: whitesmoke;

          .my-profile {
            @include position.position($type: relative);

            img {
              &:nth-child(1) {
                @include other.img-responsive($width: 100%, $max-w: 250px);
                @include position.position($type: absolute);
                z-index: 1;
                -webkit-mask-image: url(../../../../icon/rectangle.svg);
                -webkit-mask-repeat: no-repeat;
                -webkit-mask-size: 100%;
                -webkit-mask-position-y: -0.1px;
              }
              &:nth-child(2) {
                @include other.img-responsive($width: 100%, $max-w: 250px);
              }
              &:nth-child(3) {
                @include other.img-responsive($width: 100%, $max-w: 35px);
                @include position.position(
                  $type: absolute,
                  $top: 10px,
                  $left: 10px
                );
              }
              &:nth-child(4) {
                @include other.img-responsive($width: 100%, $max-w: 40px);
                @include position.position(
                  $type: absolute,
                  $left: -19px,
                  $bottom: 90px
                );
              }
              &:nth-child(5) {
                @include other.img-responsive($width: 100%, $max-w: 100px);
                @include position.position(
                  $type: absolute,
                  $top: 20px,
                  $right: 40px
                );
              }
            }
          }
        }
        .hero-right {
          margin: 2em;

          h1 {
            font-size: 1rem;
            display: none;
          }
          h2 {
            display: inline-block;
            font-size: 2.5rem;
            font-weight: bold;
          }
          small {
            margin-left: 10px;
            display: inline;
          }
          span {
            max-width: 500px;
            display: inline-block;
            font-size: map-get(
              variable-font.$primary-text-size,
              "hero-tagline-desk"
            );
          }

          .call-to-action {
            // background-color: beige;
            margin-top: 20px;
            @include display.flex-box($direction: row, $align: center);

            a {
              text-decoration: none;
              margin-right: 10px;
              @include button.button(
                $type: "custom",
                $width: fit-content,
                $height: fit-content,
                $padding: 15px 20px,
                $bg: gray,
                $b-type: none,
                $round: 8px,
                $color: white,
                $cursor: pointer,
                $hover: true,
                $hov-bg: white,
                $hov-color: black
              );
            }
          }
        }
      }
    }
    &:nth-child(2) {
      background-color: map-get(variable.$primary-color, "galery-section");
      .galery {
        .galery-videos {
          max-width: 1200px;
          margin: 0 auto;
          padding: 0 2em;
          .galery-head {
            padding-top: 3.5em;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 15px;
            .tutorial-title {
              display: flex;
              align-items: center;
              img {
                width: 35px;
              }
              h2 {
                // margin-left: 10px;
                margin-left: 2px;
                font-size: map-get(
                  variable-font.$primary-text-size,
                  "main-title"
                );
              }
            }
            .search-icon {
              img {
                width: 27px;
                transform: translateY(2.5px);
                background-color: gainsboro;
                padding: 5px;
                border-radius: 2px;
                cursor: pointer;
              }
            }
          }
          .tutorial-search {
            margin-top: 1em;
            input {
              @include form.input();
              margin-bottom: 0.5em;
            }
            .search-list {
              background-color: #ffffff;
              padding: 0.5em 1em;
              margin-bottom: 0.1em;
              a {
                text-decoration: none;
                color: inherit;
              }
            }
          }

          .image-gallery {
            margin-top: 1.5em;
            // @include media-query.media-query("galery");
            @include media-query.media-query("galery-list");
            a {   
              // if using flex galery
              // &:last-child {
              //   // background-color: #21212b;
              //   margin-right: auto;
              // }
              text-decoration: none;
              color: inherit;
              .card {
                position: relative;
                .tagline {
                  margin: 20px;
                  .title {
                    h3 {
                      font-size: 1.1rem;
                      margin-bottom: 4px;
                      margin-right: 20px;
                    }
                    p {
                      font-size: 0.7rem;
                      text-overflow: ellipsis;
                      overflow: hidden;
                      width: 200px;
                      display: -webkit-box !important;
                      -webkit-line-clamp: 2;
                      -webkit-box-orient: vertical;
                    }
                    small {
                      position: absolute;
                      right: 0;
                      top: 0;
                      padding: 0.3em 0.5em;
                      border-radius: 0.5em;
                      font-size: 0.8rem;
                      color: white;
                      margin: 0.55em;
                      background: rgba(255, 255, 255, 0.2);
                      box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
                      backdrop-filter: blur(5px);
                      -webkit-backdrop-filter: blur(5px);
                    }
                  }
                }
              }
            }
          }
        }
        .galery-features {
          span {
            display: flex;
            align-items: center;
            margin-top: 3em;
            justify-content: center;
            img {
              width: 30px;
            }
            h2 {
              // margin-left: 10px;
              &.features {
                font-size: map-get(
                  variable-font.$primary-text-size,
                  "main-title"
                );
              }
            }
          }

          small {
            margin-top: 0.2em;
            margin-bottom: 2em;
            display: inline-block;
          }
          a {
            margin-top: 1em;
            text-decoration: none;
            margin-right: 10px;
            @include button.button(
              $type: "custom",
              $width: fit-content,
              $height: fit-content,
              $padding: 15px 20px,
              $bg: gray,
              $b-type: none,
              $round: 8px,
              $color: white,
              $cursor: pointer,
              $hover: true,
              $hov-bg: #20202b,
              $hov-color: white
            );
          }
          .features-wrap {
            @include media-query.media-query("galery-features");
            .features-left {
              flex: 1;
              padding: 2em 4em;
              background-color: #efefef;
              img {
                max-width: 100%;
                width: 250px;
                height: 280px;
                object-fit: cover;
                border-radius: 3px;
              }
            }
            .features-right {
              flex: 1;
              background-color: #d8d8d8;
              padding: 2em 4em;

              img {
                max-width: 100%;
                width: 250px;
                height: 280px;
                object-fit: cover;
                border-radius: 3px;
              }
            }
            .features-title {
              margin-top: 0.5em;
              h4 {
                font-size: 1.5rem;
              }
              button {
                margin-top: 1em;
              }
            }
          }
        }
      }
    }
    &:nth-child(3) {
      background-color: map-get(variable.$primary-color, "skill-section");
      .sec {
        padding: 2em;
        max-width: 1200px;
        margin: 0 auto;
        .interest {
          padding: 2em;
          background-image: linear-gradient(
            to right,
            map-get(variable.$gradient-color, "grad-1"),
            map-get(variable.$gradient-color, "grad-2")
          );
          @include media-query.media-query("interest");
        }
        .skill {
          margin-top: 5em;
          height: 200px;

          h2 {
            text-align: center;
            margin-bottom: 2em;
            font-size: 1.2rem;
          }
          .soft {
            @include display.flex-box($direction: row, $justify: center);
            margin: 0 auto;
            max-width: 300px;
            a {
              flex: 1;
              text-align: center;
              img {
                width: 60px;
              }
            }
          }
        }
      }
    }
  }
}
