@use "./burger-strip" as burger;
@use "../display" as display;

@mixin burger-navigation() {
  .wrap {
    @include display.flex-box($justify: center, $wrap: wrap);
    width: 100%;

    .burger {
      min-width: 40px;
      height: 40px;
      border-radius: 10px;
      // border: 1.5px solid white;
      background-color: gray;
      cursor: pointer;
      @include display.flex-box($justify: center, $align: center);
      padding: 5px;
      transition: background-color 0.5s ease;
      &:first-child {
        @include burger.burger-strip(
          $type: "mode-2",
          $colors-strip-first: white,
          $colors-strip-second: white,
          $margin: 5px,
          $width-strip: 25px,
          $strip-bold: 3px,
          $bg: rgb(189, 189, 189)
        );
      }
    }
  }
}
