@mixin input() {
  transition: border 0.2s, outline 0.2s;
  &.text {
    &::placeholder {
      color: gray;
    }
    border: 1.5px solid rgb(207, 207, 207);
    width: 100%;
    padding: 10px;
    font-size: 1.05em;
    font-weight: 300;
    color: black;
    display: block;
    border-radius: 0.25em;
    // margin-bottom: 0.5em;
    &:focus {
      border: 1.5px solid #20202b;
      outline: 3.5px solid rgba($color: #f39c12, $alpha: 0.8);
    }
  }
  &.email {
    &::placeholder {
      color: gray;
    }
    border: 1.5px solid rgb(207, 207, 207);
    width: 100%;
    padding: 10px;
    font-size: 1.2em;
    font-weight: 300;
    color: black;
    display: block;
    border-radius: 0.25em;
    // margin-bottom: 0.5em;
    &:focus {
      border: 1.5px solid #20202b;
      outline: 3.5px solid rgba($color: #f39c12, $alpha: 0.8);
    }
  }
  &.date {
    border: 1.5px solid rgb(207, 207, 207);
    width: 100%;
    padding: 10px;
    font-size: 1.2em;
    color: gray;
    display: block;
    border-radius: 0.25em;
    // margin-bottom: 0.25em;
    &:focus {
      border: 1.5px solid #20202b;
      outline: 3.5px solid rgba($color: #f39c12, $alpha: 0.8);
    }
  }
  &.password {
    border: 1.5px solid rgb(207, 207, 207);
    width: 100%;
    padding: 10px;
    font-size: 1.2em;
    color: gray;
    display: block;
    border-radius: 0.25em;
    // margin-bottom: 0.5em;
    font-weight: 300;
    &:focus {
      border: 1.5px solid #20202b;
      outline: 3.5px solid rgba($color: #f39c12, $alpha: 0.8);
    }
  }
  &.telepon {
    border: 1.5px solid rgb(207, 207, 207);
    width: 100%;
    padding: 10px;
    font-size: 1.2em;
    font-weight: 300;
    color: gray;
    display: block;
    border-radius: 0.25em;
    // margin-bottom: 0.5em;
    &:focus {
      border: 1.5px solid #20202b;
      outline: 3.5px solid rgba($color: #f39c12, $alpha: 0.8);
    }
  }
  &.file {
    &::placeholder {
      color: gray;
    }
    &[type="file"] {
      color: black;
      font-size: 1rem;
    }
    &[type="file"]::-webkit-file-upload-button {
      &:hover {
        background: #fff;
        border: 2px solid #535353;
        color: #000;
      }
      margin-right: 15px;
      background: gray;
      border: 2px solid gray;
      border-radius: 5px;
      color: #fff;
      cursor: pointer;
      font-size: 12px;
      outline: none;
      padding: 13px 28px;
      text-transform: uppercase;
    }
    width: fit-content;
    padding: 1.5px;
    font-size: 1.2em;
    color: black;
    display: block;
    border-radius: 0.25em;
    // margin-bottom: 0.25em;
  }
}
@mixin select() {
  select {
    &.select {
      border: 1.5px solid rgb(207, 207, 207);
      width: 100%;
      padding: 10px;
      font-size: 1.2em;
      color: black;
      display: block;
      border-radius: 0.25em;
      margin-bottom: 0.25em;
      option {
        color: black;
      }
    }
  }
}
@mixin text-area() {
  textarea {
    &.text-area {
      border: 1.5px solid rgb(207, 207, 207);
      width: 100%;
      max-height: 150px;
      font-size: 1.2em;
      color: black;
      display: block;
      border-radius: 0.25em;
      margin-bottom: 0.25em;
      option {
        color: black;
      }
    }
  }
}

@mixin check-box() {
  div {
    &.checkbox {
      margin-top: 20px;
      display: flex;
      align-items: center;
      input {
        background-color: #2660ff;
        width: 18px;
        height: 18px;
      }
      p {
        font-size: 0.85em;
        color: black;
        margin-left: 5px;
      }
    }
  }
}
