@use "../../component/button" as button;
@use "../../component/form" as form;
@use "../../component/media-query" as media-query;

@mixin home-section-detail {
  section {
    &:nth-child(1) {
      .detail-home-wrapper {
        background-color: #cdcdcd;
        .detail-home {
          max-width: 1200px;
          margin: 0 auto;
          padding: 1em 2.5em;

          .detail-head-title {
            .breadcrumb {
              display: flex;
              background-color: #e9e9e9;
              padding: 10px 20px;
              border-radius: 30px;
              width: fit-content;
              .breadcrumb-item {
                font-size: 0.8rem;
                a {
                  text-decoration: none;
                  color: gray;
                }
                &:not(:last-child) {
                  &::after {
                    padding: 0 0.5em;
                    content: "/";
                  }
                }
              }
            }
          }
          .detail-video-wrapper {
            max-width: 800px;
            margin: 0 auto;
            padding: 2em 2em;
            min-height: 40vh;

            .detail-video {
              position: relative;
              padding-bottom: 56.25%;
              padding-top: 30px;
              height: 0;
              // overflow: hidden;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              text-align: center;
              h2 {
                font-size: 1.5rem;
              }
              p {
                font-size: 0.9rem;
              }
              iframe {
                transform: translateY(5.5em);
                position: absolute;
                top: 0;
                width: 100%;
                height: 85%;
              }
            }
          }
        }
      }
    }
    &:nth-child(2) {
      .detail-description-wrapper {
        background-color: white;
        .detail-description {
          max-width: 1200px;
          margin: 0 auto;
          // min-height: 500px;
          padding: 1em;
          display: flex;
          flex-wrap: wrap;
          @include media-query.media-query("detail-description");
          .detail-description-left {
            // background-color: antiquewhite;
            padding: 1.5em;
            flex: 3;
            h2 {
              font-size: 1.5rem;
              margin-bottom: 0.5em;
            }
            p {
              &:nth-child(2) {
                margin-bottom: 2em;
              }
            }
            .detail-description-download {
              display: flex;
              flex-direction: column;
              align-items: center;
              h3 {
                margin-bottom: 0.5em;
              }
              a {
                margin-bottom: 0.5em;
                text-decoration: none;
                box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.08),
                  0 6px 10px 0 rgba(0, 0, 0, 0.08);
                @include button.button(
                  $type: "custom",
                  $width: fit-content,
                  $height: fit-content,
                  $padding: 15px 20px,
                  $bg: gray,
                  $b-type: none,
                  $round: 8px,
                  $color: white,
                  $cursor: pointer,
                  $hover: true,
                  $hov-bg: white,
                  $hov-color: black
                );
              }
              p {
                font-size: 12px;
                text-align: center;
              }
            }
          }
          .detail-description-right {
            // background-color: aquamarine;
            flex: 1;
            padding: 1.5em;
            h2 {
              font-size: 1.2rem;
              margin-bottom: 0.5em;
            }
            img {
              width: 50px;
              border-radius: 10px;
              margin-right: 0.5em;
              box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.08),
                0 6px 10px 0 rgba(0, 0, 0, 0.08);
            }
          }
        }
      }
    }
    &:nth-child(3) {
      .detail-comment-wrapper {
        background-color: white;
        .detail-comment {
          max-width: 1200px;
          margin: 0 auto;
          // min-height: 500px;
          padding: 2.5em;
          display: flex;
          flex-direction: column;
          gap: 0.5em;
          .input-comment {
            flex: 1;
            .head-comment {
              max-width: 800px;
              display: flex;
              justify-content: space-between;
              align-items: center;
              h2 {
                font-size: 1.2rem;
              }
              p {
                font-size: 0.8rem;
              }
            }
            .input-comment-form {
              max-width: 800px;
              margin-top: 1em;
              form {
                input {
                  margin-bottom: 0.8em;
                }
                input {
                  @include form.input();
                }
                button {
                  text-decoration: none;
                  margin-bottom: 1em;
                  margin-right: 0.5em;
                  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.08),
                    0 6px 10px 0 rgba(0, 0, 0, 0.08);
                  @include button.button(
                    $type: "custom",
                    $width: fit-content,
                    $height: fit-content,
                    $padding: 10px 15px,
                    $bg: gray,
                    $b-type: none,
                    $round: 7px,
                    $color: white,
                    $cursor: pointer,
                    $hover: true,
                    $hov-bg: white,
                    $hov-color: black
                  );
                }
              }
            }
          }
          .comment-rate {
            flex: 1;
            max-width: 800px;
            margin-top: 1em;
            max-height: 400px;
            // overflow-y: auto;
            // padding: 0.5em 1em;
            // transform: translateX(-1em);
           
            .card-comment-rate {
              border-radius: 5px;
              box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.08),
                0 6px 10px 0 rgba(0, 0, 0, 0.08);

              padding: 1em;
              margin-bottom: 0.5em;
              p {
                font-size: 0.9rem;
              }
              .head-comment {
                display: flex;
                align-items: center;
                h4 {
                  font-size: 0.9rem;
                }
                small {
                  margin-left: 10px;
                  font-size: 0.6rem;
                  transform: translateY(0.5px);
                }
              }
            }
          }
        }
      }
    }
    &:nth-child(4) {
      .galery-videos {
        max-width: 1200px;
        margin: 0 auto;
        padding: 0 2.5em;
        .galery-head {
          padding-top: 1.5em;
          display: flex;
          justify-content: space-between;
          align-items: center;
          gap: 15px;
          .tutorial-title {
            display: flex;
            flex-direction: column;
            h2 {
             font-size: 1.3rem;
            }
          }
          
        }
        .tutorial-search {
          margin-top: 1em;
          @include form.input();
        }

        .image-gallery {
          margin-top: 1.5em;
          margin-bottom: 4em;
          @include media-query.media-query("galery-list");
          a {
            &:last-child {
              // background-color: #21212b;
              margin-right: auto;
            }
            text-decoration: none;
            color: inherit;
            .tagline {
              margin: 20px;
              .title {
                h3 {
                  font-size: 1.1rem;
                  margin-bottom: 4px;
                  margin-right: 20px;
                }
                p {
                  font-size: 0.7rem;
                  text-overflow: ellipsis;
                  overflow: hidden;
                  width: 200px;
                  display: -webkit-box !important;
                  -webkit-line-clamp: 2;
                  -webkit-box-orient: vertical;
                }
              }
            }
          }
        }
      }
    }
  }
}
