@use "../../base/color-variable" as variable;
@use "../../base/font-variable" as variable_font;
@use "../../component/media-query" as media-query;

footer {
  @include media-query.media-query("footer");
  background-color: map-get(variable.$primary-color, "footer");
  color: gainsboro;
  .getting {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    @include media-query.media-query("grid-footer");
    gap: 1.5em;
    margin-bottom: 2.5em;
    h2 {
      font-size: 1.5rem;
      font-weight: bold;
      margin-bottom: 10px;
    }
    p {
      max-width: 300px;
      color: rgb(156, 156, 156);
    }
    div {
      span {
        margin-bottom: 1em;
        font-weight: bold;
        display: inline-block;
        font-size: 1.1rem;
      }
      a {
        margin-bottom: 0.5em;
        text-decoration: none;
        color: rgb(156, 156, 156);
        display: block;
        width: fit-content;
        &:hover {
          color: white;
        }
      }
    }
    .getting-touch {
      .contact-wrapper {
        .email {
          margin-bottom: 0.8em;
          display: flex;
          align-items: center;
          a {
            margin-left: 1em;
            margin-bottom: 0;
          }
        }
        .phone {
          margin-bottom: 0.8em;
          display: flex;
          align-items: center;
          a {
            margin-left: 1em;
            margin-bottom: 0;
          }
        }
        .location {
          margin-bottom: 0.8em;
          display: flex;
          align-items: center;
          a {
            margin-left: 1em;
            margin-bottom: 0;
          }
          p {
            display: inline-block;
            margin-left: 1em;
            color: rgb(156, 156, 156);
          }
        }
      }
    }
  }
  small {
    display: block;
    color: rgb(156, 156, 156);
    font-size: 0.8rem;
    background-color: #15151c;
    width: fit-content;
    border-radius: 5px;
    margin: 0 auto;
    padding: 5px 10px;
    white-space: nowrap;
  }
}
