@mixin flex-galery(
  $direction: row,
  $align: stretch,
  $justify: flex-start,
  $r-gap: null,
  $c-gap: null,
  $wrap: nowrap,
  $f-basis: null
) {
  display: flex;
  flex-direction: $direction;
  align-items: $align;
  justify-content: $justify;
  row-gap: $r-gap;
  column-gap: $c-gap;
  flex-wrap: $wrap;

  a {
    position: relative;
    // background-color: #21212b;
    border-radius: 10px;
    overflow: hidden;
    // outline: 1px solid gray;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.08), 0 6px 10px 0 rgba(0, 0, 0, 0.08);

    flex-basis: $f-basis; /* width: 350px; */

    img {
      object-fit: cover;
      width: 100%;
      height: 150px;
    }
  }
}

@mixin grid-galery() {
  display: grid;
  // grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 1.5em;
  a {
    border-radius: 10px;
    overflow: hidden;
    // outline: 1px solid gray;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.08), 0 6px 10px 0 rgba(0, 0, 0, 0.08);
    img {
      object-fit: cover;
      width: 100%;
      height: 150px;
    }
  }
}
