// base button
// shadow button
// border button
// anim button

@mixin button(
  $type: "",
  $width: 80px,
  $height: 80px,
  $round: 0,
  $color: gray,
  $bg: black,
  $font: 1rem,
  $display: inline-block,
  $b-size: 1px,
  $b-type: solid,
  $b-color: white,
  $cursor: default,
  $hover: false,
  $adj-sat: 5%,
  $adj-light: 5%,
  $hov-bg: black,
  $hov-color: black,
  $hov-b-type: null,
  $active: null,
  $active-bg: null,
  $active-color: null,
  $active-b-type: null,
  $hover-adj: null,
  $padding: null
) {
  @if $type == "small" {
    width: 150px;
    height: 50px;
    color: $color;
    cursor: pointer;
    border-radius: $round;
    background-color: $bg;
    font-size: $font;
    display: $display;
    border: $b-size $b-type $b-color;

    @if $hover {
      &:hover {
        background-color: adjust-color(
          $color: $bg,
          $saturation: 5%,
          $lightness: -5%
        );
      }
    }
    @if $active {
      &.active {
      }
    }
  } @else if $type == "medium" {
    width: 200px;
    height: 80px;
    color: $color;
    cursor: pointer;
    border-radius: $round;
    background-color: $bg;
    font-size: $font;
    display: $display;
    border: $b-size $b-type $b-color;

    @if $hover {
      &:hover {
        background-color: adjust-color(
          $color: $bg,
          $saturation: 5%,
          $lightness: -5%
        );
      }
    }
    @if $active {
      &.active {
      }
    }
  } @else if $type == "big" {
    width: 300px;
    height: 80px;
    color: $color;
    cursor: pointer;
    border-radius: $round;
    background-color: $bg;
    font-size: $font;
    display: $display;
    border: $b-size $b-type $b-color;

    @if $hover {
      &:hover {
        background-color: adjust-color(
          $color: $bg,
          $saturation: 5%,
          $lightness: -5%
        );
      }
    }
    @if $active {
      &.active {
      }
    }
  } @else if $type == "custom" {
    width: $width;
    height: $height;
    color: $color;
    cursor: $cursor;
    border-radius: $round;
    background-color: $bg;
    font-size: $font;
    display: $display;
    border: $b-size $b-type $b-color;
    padding: $padding;

    @if $hover {
      &:hover {
        background-color: $hov-bg;
        color: $hov-color;
        border: $hov-b-type;
      }
    }

    @if $hover-adj {
      &:hover {
        background-color: adjust-color(
          $color: $bg,
          $saturation: $adj-sat,
          $lightness: $adj-light
        );
      }
    }

    @if $active {
      &.active {
        background-color: $active-bg !important;
        color: $active-color !important;
        border: $active-b-type !important;
      }
    }
  }
}
