@use "../../base/color-variable" as variable;
@use "../../base/font-variable" as variable_font;

@mixin dekstop-navigation() {
  li {
    display: inline-block;
    color: map-get(variable.$primary-color, "base-text-color");
    a {
      &.active {
        color: map-get(variable.$primary-color, "active-nav-text-color");
        font-weight: medium;
        // background-color: map-get(
        //   variable.$primary-color,
        //   "active-nav-background"
        // );
      }
      &:not(.active):hover {
        color: map-get(variable.$primary-color, "hover-nav-text-color");
        font-weight: medium;
        // background-color: map-get(
        //   variable.$primary-color,
        //   "hover-nav-background"
        // );
      }
      color: inherit;
      display: inline-block;
      text-decoration: none;
      // background-color: burlywood;
      padding: 1.4em;
      font-size: map-get(variable_font.$primary-text-size, "text-nav");
    }
  }
}
