@use "../../base/font-variable" as variable_font;
@use "../../component/media-query" as media_query;
@use "../../base/color-variable" as variable;
@use "../../component/transition/slide-menu" as slide;
@use "../../component/position" as position;

header {
  nav {
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.08), 0 6px 10px 0 rgba(0, 0, 0, 0.08);
    @include position.position($type: fixed, $top: 0, $left: 0, $right: 0);
    background-color: map-get(variable.$primary-color, "navigation");
    z-index: 999;
    min-height: 68px;
    .brand-menu {
      font-size: map-get(variable_font.$primary-text-size, "brand");
      a {
        text-decoration: none;
        color: inherit;
      }
    }
    @include media_query.media-query("navigation");
  }
  @include slide.slide-menu("standart");
}
