@mixin inline() {
  display: inline;
}
@mixin inline-block() {
  display: inline-block;
}
@mixin block() {
  display: block;
}

@mixin flex-box(
  $direction: row,
  $align: stretch,
  $justify: flex-start,
  $wrap: nowrap
) {
  display: flex;
  flex-direction: $direction;
  align-items: $align;
  justify-content: $justify;
  flex-wrap: $wrap;
}

@mixin grid() {
  display: grid;
}

@mixin table() {
  display: table;
}
