@mixin transform($type, $value: null, $value2: null) {
  @if $type == "translate" {
    transform: translate($value, $value2);
  } @else if $type == "scale" {
    transform: scale($value);
  } @else if $type == "scaleXY" {
    transform: scale($value, $value2);
  } @else if $type == "rotate" {
    transform: rotate($value, $value2);
  }
}

@mixin transform-translate($type, $value) {
  @if $type == "translateX" {
    transform: translateX($value);
  } @else if $type == "translateY" {
    transform: translateY($value);
  } @else if $type == "translateZ" {
    transform: translateZ($value);
  }
}

@mixin transform-rotate($type, $value) {
  @if $type == "rotateX" {
    transform: rotateX($value);
  } @else if $type == "rotateY" {
    transform: rotateY($value);
  } @else if $type == "rotateZ" {
    transform: rotateZ($value);
  }
}
