@use "../../component/tranform" as transform;
@use "../../base/color-variable" as variable;

@mixin slide-menu($type) {
  @if $type == "standart" {
    #menu {
      position: fixed;
      top: 0;
      width: 100vw;
      height: 100vh;
      opacity: 0.8;
      z-index: 99;
      background-color: rgb(0, 0, 0);
      padding: 50px;
      @include transform.transform-translate("translateX", 100%);
      &.close {
        transition: 1s cubic-bezier(0.23, 1, 0.32, 1);
      }
      &.show {
        transition: 1s cubic-bezier(0.23, 1, 0.32, 1);
        @include transform.transform-translate("translateX", 0%);
      }
      .wrap {
        display: flex;
        margin-top: 70px;
        ul {
          text-align: right;

          flex: 1;
          li {
            margin-bottom: 20px;
            a {
              display: inline-block;
              color: inherit;

              &:hover {
                color: map-get(variable.$primary-color, "hover-nav-text");
              }
              text-decoration: none;
              font-size: 1.3rem;
            }
          }
        }
        span {
          a {
            color: inherit;
            text-decoration: none;
            flex: 1;
            // background-color: aqua;
            align-self: flex-start;
            display: flex;
            span {
              margin-left: 8px;
            }
          }
        }
      }
      div {
        &.footer-nav {
          p {
            text-align: center;
            border-radius: 5px;
            border: 1px solid white;
            padding: 5px;
            margin-bottom: 10px;
          }
          span {
            a {
              color: inherit;
              &:first-child {
                margin-left: 5px;
              }
              margin-right: 10px;
            }
          }
        }
      }
    }
  } @else if $type == "slide-1" {
  } @else if $type == "slide-2" {
  } @else if $type == "slide-3" {
  }
}
