@use "../../component/form" as form;
@use "../../base/font-variable" as variable-font;
@use "../../component/media-query" as media-query;
@use "../../base/color-variable" as variable;

@mixin home-section-list {
  section {
    background-color: map-get(variable.$primary-color, "galery-section");
    .galery-list {
      max-width: 1200px;
      margin: 0 auto;
      min-height: 450px;
      padding: 2em;
      .galery-head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 15px;
        .list-title {
          display: flex;
          align-items: center;
          img {
            width: 35px;
          }
          h2 {
            margin-left: 10px;
            font-size: map-get(variable-font.$primary-text-size, "main-title");
          }
        }
        .search-icon {
          img {
            width: 27px;
            transform: translateY(2.5px);
            background-color: gainsboro;
            padding: 5px;
            border-radius: 2px;
            cursor: pointer;
          }
        }
      }
      .list-search {
        margin-top: 1em;
        input {
          @include form.input();
        }
      }
      .image-gallery-list {
        margin-top: 1.5em;
        @include media-query.media-query("galery-list");
        a {
          text-decoration: none;
          color: inherit;
          .card {
            position: relative;
            .tagline {
              margin: 20px;
              .title {
                h3 {
                  font-size: 1.1rem;
                  margin-bottom: 4px;
                  margin-right: 20px;
                }
                p {
                  font-size: 0.7rem;
                  text-overflow: ellipsis;
                  overflow: hidden;
                  width: 200px;
                  display: -webkit-box !important;
                  -webkit-line-clamp: 2;
                  -webkit-box-orient: vertical;
                }
              }
            }
          }
        }
      }
    }
  }
}
