@mixin burger-strip(
  $type,
  $colors-strip-first,
  $colors-strip-second,
  $margin,
  $width-strip,
  $strip-bold,
  $bg
) {
  @if $type == "standart" {
    // &:hover {
    //   background: $bg;
    //   .strip {
    //     div {
    //       background-color: $colors-strip-second;
    //       &:first-child {
    //         // transform: translateY(13px) rotate(45deg);
    //       }
    //       &:nth-child(2) {
    //         // opacity: 0;
    //       }
    //       &:last-child {
    //         // transform: translateY(-13px) rotate(-45deg);
    //       }
    //     }
    //   }
    // }
    &.active {
      background: $bg;
      .strip {
        div {
          background-color: $colors-strip-second;
          &:first-child {
            // transform: translateY(13px) rotate(45deg);
          }
          &:nth-child(2) {
            // opacity: 0;
          }
          &:last-child {
            // transform: translateY(-13px) rotate(-45deg);
          }
        }
      }
    }
    .strip {
      div {
        height: 3px;
        border-radius: 2px;
        background: $colors-strip-first;
        margin: $margin;
        transition: all 0.55s cubic-bezier(0.075, 0.82, 0.165, 1);
        width: $width-strip;
      }
    }
  } @else if $type == "mode-1" {
    // &:hover {
    //   background: $bg;
    //   .strip {
    //     div {
    //       background-color: white;
    //       &:first-child {
    //         transform: translateY($margin + $strip-bold) rotate(45deg);
    //       }
    //       &:nth-child(2) {
    //         opacity: 0;
    //       }
    //       &:last-child {
    //         transform: translateY(-#{$margin + $strip-bold}) rotate(-45deg);
    //       }
    //     }
    //   }
    // }
    &.active {
      background: $bg;
      .strip {
        div {
          background-color: white;
          &:first-child {
            transform: translateY($margin + $strip-bold) rotate(45deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:last-child {
            transform: translateY(-#{$margin + $strip-bold}) rotate(-45deg);
          }
        }
      }
    }
    .strip {
      div {
        height: 3px;
        border-radius: 2px;
        background: rgb(56 56 56);
        margin: $margin;
        transition: all 0.55s cubic-bezier(0.075, 0.82, 0.165, 1);
        width: $width-strip;
      }
    }
  } @else if $type == "mode-2" {
    // &:hover {
    //   background: $bg;
    //   .strip {
    //     div {
    //       background-color: $colors-strip-second;
    //       &:first-child {
    //         transform: translateY($margin + $strip-bold);
    //         width: $width-strip;
    //       }
    //       &:nth-child(2) {
    //         opacity: 0;
    //       }
    //       &:last-child {
    //         transform: translateY(-#{$margin + $strip-bold});
    //         width: $width-strip;
    //       }
    //     }
    //   }
    // }
    &.active {
      background: $bg;
      .strip {
        div {
          background-color: $colors-strip-second;
          &:first-child {
            transform: translateY($margin + $strip-bold);
            width: $width-strip;
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:last-child {
            transform: translateY(-#{$margin + $strip-bold});
            width: $width-strip;
          }
        }
      }
    }
    .strip {
      div {
        height: $strip-bold;
        border-radius: 2px;
        background: $colors-strip-first;
        margin: $margin auto;
        transition: transform 0.55s cubic-bezier(0.075, 0.82, 0.165, 1);
        width: $width-strip;
      }
    }
  } @else if $type == "mode-3" {
    // &:hover {
    //   background: $bg;
    //   .strip {
    //     transform: scale(0.7) rotate(45deg);
    //     transition: all 0.25s ease 0.05s;
    //     div {
    //       background-color: $colors-strip-second;
    //       &:first-child {
    //         transform: translateY($margin + $strip-bold) rotate(0deg);
    //       }
    //       &:nth-child(2) {
    //         opacity: 0;
    //       }
    //       &:last-child {
    //         transform: translateY(-#{$margin + $strip-bold}) rotate(90deg);
    //       }
    //     }
    //   }
    // }
    &.active {
      background: $bg;
      .strip {
        transform: scale(0.7) rotate(45deg);
        transition: all 0.25s ease 0.05s;
        div {
          background-color: $colors-strip-second;
          &:first-child {
            transform: translateY($margin + $strip-bold) rotate(0deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:last-child {
            transform: translateY(-#{$margin + $strip-bold}) rotate(90deg);
          }
        }
      }
    }
    .strip {
      div {
        height: $strip-bold;
        border-radius: 2px;
        background: $colors-strip-first;
        margin: $margin auto;
        transition: all 0.65s cubic-bezier(0.075, 0.82, 0.165, 1);
        width: $width-strip;
      }
    }
  }
}
